#body
{
  font-family:"Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
}

.App {
  background-color: white;
}

.App .header {
  background-color:rgb(244, 244, 244);
  color: white;
}



.App .body .sidebar {
  background-color: rgb(244, 244, 244);
}

.App {
  min-height: 100vh; 
}
.App {
  display: flex;
  flex-direction: column;
}

.App .body {
  display: flex;
  flex-direction: row;
}

.login {
 float: right; 
}


.body .sidebar {
  order: -1;
  max-width: 20em;
  flex-grow: 0;    /* Defined the ability to groe */
  flex-shrink: 0;  /* Defines the ability to shrink */
  flex-basis: 20em;/* Default value of the element before distribuing the remaing space */
}

.App .header,
.App .footer {
  height: 3em;

}

.NavBar {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 0.5em ;
  color: white;
  background-color: rgb(0, 120, 212);
  justify-content: space-between;
}